import React, { lazy } from 'react';
import { Routes, Route } from 'react-router-dom';

import { Icons } from '@miq/componentjs';
import Staff from '@miq/staffjs';

const PlannerStaffView = lazy(() => import('./PlannerView'));
const CaptionStaffRoutes = lazy(() => import('./CaptionRoutes'));
const HashtagStaffRoutes = lazy(() => import('./HashtagRoutes'));
const SettingRoutes = lazy(() => import('./Setting/'));

export default function SocialStaffRoutes(props) {
  console.log(props);

  return (
    <Staff.View
      header={
        <div className="d-flex align-items-center justify-content-between py-2">
          <div className="">
            <Staff.NavLink to="/staff/social" className="btn me-1">
              Planner
            </Staff.NavLink>
            <Staff.NavLink to="captions" className="btn me-1">
              Captions
            </Staff.NavLink>
            <Staff.NavLink to="hashtags" className="btn">
              Hashtags
            </Staff.NavLink>
          </div>

          <div>
            <Staff.Link to="settings">
              <Icons.Gear />
            </Staff.Link>
          </div>
        </div>
      }
    >
      <Routes>
        <Route path="captions/*" element={<CaptionStaffRoutes />} />
        <Route path="hashtags/*" element={<HashtagStaffRoutes />} />
        <Route path="settings/*" element={<SettingRoutes />} />
        <Route path="/" element={<PlannerStaffView />} />
      </Routes>
    </Staff.View>
  );
}
